import axios from 'axios';

export default {
    name: 'LoginPage',
    data() {
        return {
            message: '',
            loginUrls: {
                naver: '',
                google: '',
                kakao: '',
            },
        };
    },
    created() {
        const apiBaseUrl = process.env.VUE_APP_API_BASE_URL;

        // 메세지 가져오기
        axios.get(`${apiBaseUrl}/user/api/login/`)
            .then(response => {
                this.message = response.data.message;
            })
            .catch(error => {
                console.error('Failed to fetch login message:', error);
            });

        // 소셜 로그인 URL 가져오기
        axios.get(`${apiBaseUrl}/api/social-login-urls/`)
            .then(response => {
                this.loginUrls = response.data;
            })
            .catch(error => {
                console.error('Failed to fetch social login URLs:', error);
            });
    },
    methods: {
        redirectToNaverLogin() {
            const apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
            window.location.href = `${apiBaseUrl}/user/naver/login`;
        },
        redirectToSocialLogin(provider) {
            const url = this.loginUrls[provider];
            if (url) {
                window.location.href = url;
            }
        }
    }
};
